* {
  box-sizing: border-box;
}
body {
  background-color: #0c2461;
  margin: 0;
}
.container {
  height: 800px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.display {
  display: flex;
  flex-wrap: wrap;
  width: 360px;
  gap: 20px 10px;
}
.display h1 {
  display: block;
  width: 100%;
  text-align: center;
}
.box {
  height: 100px;
  width: 100px;
  padding: 33px 38px;
  text-transform: uppercase;
  border-radius: 8px;
  font-size: 25px;
  font-weight: 500;
  color: #ffffff80;
  text-shadow: none;
  border: 1px solid #ffffff80;
  transition: 0.5s ease;
  user-select: none;
}
#box {
  cursor: pointer;
  transition: all 0.1s ease-in-out;
  &:active {
    color: #ffffff;
  background: #008cff;
  border: 1px solid #008cff;
  text-shadow: 0 0 5px #ffffff,
              0 0 10px #ffffff,
              0 0 20px #ffffff;
  box-shadow: 0 0 5px #008cff,
              0 0 20px #008cff,
              0 0 50px #008cff,
              0 0 100px #008cff;
  }
}
.drum-pad.active {
  transition: all ease-in-out;
  color: #ffffff;
  background: #008cff;
  border: 1px solid #008cff;
  text-shadow: 0 0 5px #ffffff,
              0 0 10px #ffffff,
              0 0 20px #ffffff;
  box-shadow: 0 0 5px #008cff,
              0 0 20px #008cff,
              0 0 50px #008cff,
              0 0 100px #008cff;
}